import React from "react";
import "./Features.styles.css";

import Facebook from "../assets/Facebook.svg";
import Google from "../assets/Google.svg";
import Instagram from "../assets/Instagram.svg";

import { BsArrowRight } from "react-icons/bs";

const services = [
  {
    icon: Facebook,
    title: "Facebook Ads",
    content:
      "With custom audience targeting and personalized ad campaigns, your business is sure to acquire leads.",
  },
  {
    icon: Google,
    title: "Google Ads",
    content:
      "We offer industry research paired with PPC monitoring and A/B testing to ensure you get an improved ROI.",
  },
  {
    icon: Instagram,
    title: "Instagram Ads",
    content:
      "Right from design & content strategy to profile management and everything in between, we’ve got you covered.",
  },
];

const Features = () => {
  return (
    <section className="features">
      <h1 className="features__title">Tailor-made Services</h1>
      <div className="features__bottom">
        {services.map(({ icon, title, content }) => (
          <div key={title} className="features__card">
            <img src={icon} alt={title} className="features__cardImage" />
            <div className="features__cardInfo">
              <h3 className="features__cardFeatureTitle">{title}</h3>
              <p className="features__cardFeatureContent">{content}</p>
            </div>
            <a href="/" className="features__cardURL">
              <p>Explore</p>
              <BsArrowRight className="features__CardURLIcon" size={14} />
            </a>
          </div>
        ))}
      </div>
    </section>
  );
};

export default Features;
