import axios from "axios";
import { SET_EMAIL } from "./reducers/user.reducer";
import { SET_ERROR_MESSAGE } from "./reducers/message.reducer";

export const changeEmailHandler = (value) => (dispatch) => {
  dispatch(SET_EMAIL(value));
};

export const registerEmail = (email) => async (dispatch) => {
  try {
    await axios.post("/api/v1/user/register-email", { email });
  } catch (err) {
    if (err) return dispatch(SET_ERROR_MESSAGE(err.message));
    dispatch(SET_ERROR_MESSAGE(err.response?.data?.message));
  }
};
