import React from "react";
import "./Testimonials.styles.css";

import Avatar from "../assets/avatar.png";

const feedbacks = [
  {
    title: "Incredibly Amazing!",
    content:
      "Zocket made a huge difference in just a few weeks of using it. Their ad copy helped take our business to the next level.",
    avatar: Avatar,
    username: "Ella May",
    userPosition: "Digital Marketer",
  },
  {
    title: "Remarkable Growth",
    content:
      "Since the day we started using Zocket, we’ve seen incredible growth in inbound enquiries and leads.",
    avatar: Avatar,
    username: "Julie Murphy",
    userPosition: "Sales Representative",
  },
  {
    title: "Awesome Features",
    content:
      "Zocket has every feature you can think of when it comes to growing your business. I highly recommend Zocket to everyone.",
    avatar: Avatar,
    username: "Eva Jenkins",
    userPosition: "Sales Lead",
  },
];

const Testimonials = () => {
  return (
    <section className="testimonials">
      <h1 className="testimonials__title">Hear It From Them</h1>
      <div className="testimonials__items">
        {feedbacks.map(
          ({ title, content, avatar, username, userPosition }, index) => (
            <div className="testimonials__item" key={index}>
              <div className="testimonials__itemTop">
                <h1 className="testimonials__itemTitle">{title}</h1>
                <p className="testimonials__itemContent">{content}</p>
              </div>
              <div className="testimonials__itemBottom">
                <div className="testimonials__itemAvatarWrapper">
                  <div className="testimonials__itemAvatarContainer">
                    <img
                      src={avatar}
                      alt="testimonial__avatar"
                      className="testimonials__itemAvatar"
                    />
                  </div>
                </div>
                <h1 className="testimonials__itemUserName">{username}</h1>
                <p className="testimonials__itemUserPosition">{userPosition}</p>
              </div>
            </div>
          )
        )}
      </div>
    </section>
  );
};

export default Testimonials;
